import React from "react"

class IndexPage extends React.Component {
  constructor(props) {
    super(props);
    this.intervalId = 0;

    this.displayValues = []

    // Add all uppercase letters to the display values.
    for (let asciiValue = 65; asciiValue <= 90; ++asciiValue) {
      this.displayValues.push(String.fromCharCode(asciiValue))
    }

    // Add digits 0 through 9 to the display values.
    for (let digit = 0; digit <= 10; ++digit) {
      this.displayValues.push(digit.toString())
    }

    this.state = { currentText: this.displayValues[0] };
  }

  componentDidMount() {
    this.start();
  }

  start() {
    this.intervalId = setInterval(() => this.updateText(), 1000)
  }

  stop() {
    if (this.intervalId !== 0) {
      clearInterval(this.intervalId);
      this.intervalId = 0;
    }
  }

  toggle() {
    if (this.intervalId === 0) {
      this.start()
    } else {
      this.stop();
    }
  }

  updateText() {
    const currentText = this.displayValues[Math.floor(Math.random() * this.displayValues.length)]
    this.setState({
      ...this.state,
      currentText
    })
  }

  // beep() {
  //   var snd = new Audio("data:audio/wav;base64,");  
  //   snd.play();
  // }

  createStyle() {
    return {
      position: "absolute",
      transform: "translate(-50%, -50%)",
      top: `${Math.random() * 80 + 10}%`,
      left: `${Math.random() * 80 + 10}%`
    }
  }

  render() {
    // this.beep()

    return (
      <div id="center-parent" style={this.createStyle()}>
        <div id="the-text" onClick={this.toggle.bind(this)}>
          <h1>{this.state.currentText}</h1>
        </div>
      </div>
    );
  }
}

export default IndexPage
